}

/* Styles spécifiques pour iPhone - Accordéons */
@media (max-width: 480px) {
  .conseils-accordion .accordion-header {
    padding: 1rem 1.5rem;
    min-height: 60px;
    touch-action: manipulation;
    -webkit-tap-highlight-color: rgba(16, 185, 129, 0.4);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
  }
  
  .conseils-accordion .accordion-header:active {
    background: #e2e8f0;
    transform: scale(0.98);
  }
}

/* Styles spécifiques pour iOS */
@supports (-webkit-touch-callout: none) {
  .conseils-accordion .accordion-header {
    -webkit-tap-highlight-color: rgba(16, 185, 129, 0.5);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    -webkit-appearance: none;
    appearance: none;
  }
  
  .conseils-accordion .accordion-header:active {
    background: #e2e8f0;
    transform: scale(0.98);
    transition: all 0.1s ease;
  }
  
  .conseils-accordion .accordion-header:focus {
    outline: 2px solid #10b981;
    outline-offset: 2px;
  }
}

